import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Col from "react-bootstrap/Col"
import { Link } from "gatsby"
import { PaperAirplaneIcon } from "@primer/octicons-react"

const NotFoundPage = () => {
  return (
    <Layout>
      <SEO title="404: Not found" />
      <section className="row mx-0 wfBackground">
        <Col style={{ paddingTop: "100px" }}>
          <h1 className="text-center pb-4">Webfabrics</h1>
        </Col>
      </section>
      <Col
        style={{ minHeight: "66vh", background: "#cecece" }}
        className="mx-auto text-center pt-4"
      >
        <h1>404 NOT FOUND</h1>
        <p>You just hit a route that doesn&#39;t exist... the sadness.</p>
        <Link
          type="button"
          role="button"
          className="pr-4 btn btn-lg btn-primary text-monospace"
          to="/"
        >
          <span className="badge badge-pill">
            <PaperAirplaneIcon size={16} aria-label="Design icon" />
          </span>
          <strong>HOME </strong>
        </Link>
      </Col>
    </Layout>
  )
}

export default NotFoundPage
